/* Upload files Style */
/* === Wrapper Styles === */
 #FileUpload {
  display: flex;
  justify-content: center;
} 


/* === Upload Box === */
.upload {
  margin: 10px;
  height: 85px;
  border: 8px dashed #e6f5e9;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.image-container{
  display: flex;
  align-items: center;
  justify-content: center;


}
.image-container > img{
  padding-top: 15px;
  object-fit: cover;
  width: 250px;
  max-height: 300px;
}

.modal-pic{
  max-width: 400px;
  max-height: 400px;
  object-fit: contain;
}

.modal-dialog,
.come-from-modal.right .modal-dialog{
  position:fixed;
  height: 100%;
  transform: translate3d(0%, 0, 0);
}

.come-from-modal.right .modal-dialog{
    right: 0px;
    transition: opacity 0.3s linear, right 0.3 ease-out;
}
.details-component{
  margin-top: 2rem;
  width: 300px !important;
}
.modal-backdrop.show{
  opacity: 0.1 !important;
}

.modal-upload .modal-dialog{
  position: initial !important;
}
.table-item{
  cursor: pointer;
}
.sort-icon{
  cursor: pointer;
} 


/* body{
  box-sizing: border-box;
  background-color: #efefef;
} */
.form-control-search{
  width: 55vw !important;
  border-radius: 1rem !important;
}
.navbar-collapse{
  flex-grow: 0 !important;
}
.busqueda-tag{
  height: 37px;
  width: 130px;
  margin-top: 10px;
}
.tag{
  border: 1px solid #e67401;
  padding: 3px 20px;
  margin: auto 10px;
  border-radius: 1rem;
  background-color: #e67401;
  color: white;
  margin-top: 10px;
}

.close{
  color: rgb(219, 214, 214);
}
.close:hover{
  cursor: pointer;
}
.icons-text{
  display: none;
}
.hover-icon:hover{
  cursor: pointer;
}
.margin-tags{
  margin-left: 140px;
}
.margin-filters{
  margin-left: 150px;
}
.margin-navbar{
  margin-left: 150px;
}
.margin-sortBy{
  margin: 10px 150px;
}
.margin-elements-container{
  margin: 10px 150px;
}


.form-select{
  border: none !important;
  width: auto !important;
}
.filter{
  margin: auto -15px !important;

}
.picture{
  width: 100%;
  height: 200px;
  z-index:1;
  position: relative;
  object-fit: contain;
}
.viewObjects{
  margin: 40px;
}

.card{
  border-radius: 1.25rem !important;
  transition: 0.5s;
  height: 260px;
}
.card:hover{
  background-color: #81c1e0;
  cursor:pointer;
}
.download-jpg{
  text-align: center;
  color: white;
  padding: 5px;
  background-color: black;
  font-size: 12px !important;
  border-radius: 1.25rem !important;
  width: 62px;
  display: block;
  position: absolute;
  top: 83%;
  left: 63%;
  opacity: 0.7;
  z-index: 2;
}

.download-jpg:hover{
  opacity: 1;
}
.modal-content{
  margin: 10px ;
}

.upload-icon{
  font-size: 30px;
}
.border-upload-modal{
  padding: 32px !important;
  border: dashed 5px #1d6aa3 !important;


}
.btn-order{
  width: 150px;
}

input[type="file"] {
  font-size: 100px;
position: absolute;
left: 0;
top: 0;
opacity: 0;
width: 100%;
height: 60%;

}
.btn-orange{
  background-color: #e67401;
  color: white;
  border: none;
  padding: 10px 25px;
  border-radius: 1rem;
}

/* .modal-container{
  width: 20%;
  height: auto;
} */

th > img{
  width: 180px;
  height: 150px;
  position: relative;
  object-fit: contain;
}
.big-list-hidden{
  display: none;
}
.details{
  color:#5f5e5e
}
.details:hover{
  cursor: pointer;
}

@media (max-width: 992px) {
  .margin-sortBy{
      margin: 10px 40px;
  }
  .margin-tags{
      margin-left: 15px;
  }
  .margin-filters{
      margin-left: 15px;
  }
  .icons-text{
      display: inline;
  }

}
@media (max-width: 767px) {
  .download-jpg{
      left: 80%;
  }
  .form-control-search{
      width: 70vw !important;
  }
  .margin-elements-container-list{
      margin-left:10%;
  }

}

@media (max-width: 806px) {

  .list-hidden{
      display: none;
  }

  .big-list-hidden{
    display: inline;
  }
}
@media (max-width: 600px) {
  .download-jpg{
      left: 70%;
  }
  .modal-content{
    width: 90%;
  }
}

@media (max-width: 500px) {
  .card{
      width: 300px;
  }
  .margin-elements-container-list{
      margin-left:10%;
  }
  .small-col{
      /* display: flex; */
      align-items: center;
      justify-content: center;
      width: 10px !important;
  }
  .tags-container{
      display: none !important;
  }
  .filters-container{
      display: none !important;
  }

  .come-from-modal.right .modal-dialog{
    right: auto;
    margin-top: 0 !important;
  }
  .details-component{
    margin-top: 0rem;
  }

}

@media (max-width: 436px) {
  th > img{
      width: 180px;
  }
  .modal-content{
    margin: 5px;
    width: 90%;
  }
  .modal-body > img{
    max-width: 200px;
    max-height: 200px;
    object-fit: contain;
  }
  .border-upload-modal {
    padding: 20px !important;
    border: dashed 5px #1d6aa3 !important;
  }
}


@media (max-width: 335px) {
  .card{
      width: 200px;
  }
  .margin-elements-container{
      margin-left:20%;
  }
  .download-jpg{
      left: 65%;
  }
  .form-control-search{
      width: 80vw !important;
  }
  .border-upload-modal {
    padding: 10px !important;
    border: dashed 5px #1d6aa3 !important;
  }
  .modal-content {
    margin: 5px;
    width: 100%;
  }
}